body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* var(--main-color) */
:root {
  --main-color: #00966b;
  --light-color: #1ac500;
  --main-color-with-opacity: rgb(0, 150, 107, 0.5);
  --main-color-with-less-opacity: rgb(0, 150, 107, 0.7);
  --secondary-color: #006875;
  --light-background-color: #919191;
  --third-background-color: #f6f6f6;
  --dark-color: #0c5541;
  --text-color: #ffff;
  --third-color: #ff6868;
  --mobile-small-text: 1rem;
  --mobile-medium-text: 1.2rem;
  --components-gap: 1rem;
  --medium-size: 2rem;
  --large-size: 3rem;
  --small-size: 1.5rem;
  --text-size: 1.2rem;
  --small-text-size: 1rem;
  --medium-text-size: 1.3rem;
  --border-color: lightgrey;
  --cc: #37afff;
  --light-text-color: #707070;
  --dark-text-color: black;
  --secondary-background-color: #f6f6f6;
  --light-text-color: #919191;
  --text-color-light: #919191;
  --gradient-color: rgb(228, 228, 228, 0.1);
  --buyer-page-margin-left: 6vw;
  --buyer-page-margin-right: 6vw;
  --talent-home-heading: 2rem;
  --stars-colors: #ffab03;
}
@media only screen and (max-width: 1050px) {
  :root {
    --buyer-page-margin-right: 3vw;
  }
}
@media only screen and (max-width: 768px) {
  :root {
    --buyer-page-margin-right: 0;
    --buyer-page-margin-left: 0;
  }
}



