@media screen and (max-width: 960px) {
  .navbar {
    border: none;
    height: 70px;
  }
  .Main_search_bar_buyer {
    display: none;
  }
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    /* height: 90vh; */
    position: absolute;
    top: 80px;
    left: -100%;
    /* bottom: -100%; */
    opacity: 1;
    transition: all 0.2s ease;
    /* display: none; */
    /* z-index: -1; */
  }

  .nav-menu.active {
    background: #ffffff;
    left: 0;
    /* background-color: aquamarine; */
    /* display: flex; */
    /* bottom: +20%; */
    opacity: 1;
    transition: all 0.2s ease;
    z-index: 1;
    padding-bottom: calc(100vh - 590px);
    overflow-y: hidden;
  }
  .nav-menu.active.activeProToolTip {
    padding-bottom: calc(125vh - 590px);
  }

  .nav-links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
  }

  .nav-links:hover {
    border-radius: 0;
  }

  .navbar-logo {
    position: absolute;
    top: 0;
    left: 0;
    -webkit-transform: translate(25%, 50%);
            transform: translate(25%, 50%);
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    -webkit-transform: translate(-100%, 60%);
            transform: translate(-100%, 60%);
    font-size: 1.5rem;
    cursor: pointer;
  }

  .fa-times {
    color: #fff;
    font-size: 2rem;
  }

  .nav-links-mobile {
    display: block;
    text-align: center;
    padding: 1.5rem;
    margin: 2rem auto;
    border-radius: 4px;
    width: 80%;
    background: var(--main-color);
    text-decoration: none;
    color: #fff;
    font-size: 1.5rem;
  }

  .nav-nav-links-mobile:hover {
    background: #fff;
    color: var(--main-color);
    transition: 250ms;
  }

  /* button {
    display: none;
  } */
}

